<template>
  <div class="registerTagBox">
    <div class="container">
      <el-form v-if="!isSuccess" ref="ruleForm" :model="ruleForm" :rules="rules" label-width="100px">
        <el-form-item label="" required prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" required prop="phone">
          <el-input v-model="ruleForm.phone"></el-input>
        </el-form-item>
        <el-form-item label="" required prop="pwd">
          <el-input v-model="ruleForm.pwd" type="password"></el-input>
        </el-form-item>
        <el-form-item label="" required prop="truePwd">
          <el-input v-model="ruleForm.truePwd" type="password"></el-input>
        </el-form-item>
        <el-form-item label="" required prop="email">
          <el-input v-model="ruleForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" required prop="vertifyMeg">
          <el-input style="width:358px;margin-right:10px" v-model="ruleForm.vertifyMeg" class="vertify-code"></el-input>
          <el-button plain :disabled="clickCodeFlag" @click="getVertifyCode">{{ codeText }}</el-button>
        </el-form-item>
        <el-form-item>
          <el-checkbox v-model="ruleForm.checked" class="check-agree"></el-checkbox>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit" round></el-button>
        </el-form-item>
      </el-form>


      <!--  -->
      <p class="reg-submit-warp" v-if="isSuccess">
        <el-button type="primary" @click="goLogin"></el-button>
        <span style="margin-left: 20px;"></span>
        <router-link to="login"></router-link>
      </p>
    </div>
  </div>

</template>
<script>
import { checkPhone } from "../base"
import { apiGetValidCodePost,apiRegisterPost } from "../api";
  export default {
    name: 'registerS',
    mounted(){
    },
    data() {
      // 
      let checkPwd = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(""));
        }
        setTimeout(() => {
          if (value === this.ruleForm.pwd) {
            callback();
          } else {
            callback(new Error(""));
          }
        }, 100);
      };
      return {
        isSuccess: false,
        ruleForm: {
          name: "",
          pwd: "",
          truePwd: "",
          email: "",
          phone: ""
        },
        clickCodeFlag: false,
        rules: {
          name: [{ required: true, message: "", trigger: "blur" }],
          pwd: [
            { required: true, message: "", trigger: "blur" },
            { min: 6, max: 18, message: "618", trigger: "blur" }
          ],
          truePwd: [{ required: true, validator: checkPwd, trigger: "blur" }],
          email: [
            { required: true, message: "Email", trigger: "blur" },
            { type: "email", message: "Email", trigger: "blur" }
          ],
          phone: [{ required: true, validator: checkPhone, trigger: "blur" }],
          vertifyMeg: [{ required: true, message: "", trigger: "blur" }]
        }
      }
    },
    computed: {
      codeText() {
        if (this.clickCodeFlag) {
          return `${this.timerNum}s`;
        }
        return "";
      }
    },
    methods: {
      onSubmit() {
        this.$refs['ruleForm'].validate((valid) => {
          if (valid) {
            let params = {
              role_id: 3,
              user_name: this.ruleForm.name,
              mobile: this.ruleForm.phone,
              password: this.ruleForm.pwd,
              re_password: this.ruleForm.truePwd,
              email: this.ruleForm.email,
              school_name: this.ruleForm.school,
              grade: this.ruleForm.eduValue,
              valid_code: this.ruleForm.vertifyMeg,
              // identity_img: this.ruleForm.imageId,
              // student_card_img: this.ruleForm.imageSchoolId
            }
            apiRegisterPost(params).then(res => {
              if(res.data.code == 200){
                this.$message.success("");
                this.isSuccess = true
              }else{
                this.$message.error(res.data.msg);
              }
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      updatePhone(e) {
        this.setMobile(e);
      },
      // 
      getVertifyCode() {
        if (this.ruleForm.phone) {
          apiGetValidCodePost({ mobile: this.ruleForm.phone }).then(res => {
            if (res.data.code == 200) {
              this.$message.success('')
              this.clickCodeFlag = true;
              this.timer = setInterval(this.cutDown, 1000);
            }else{
              this.$message.error(res.data.msg);
            }
          });
        }
      },
      goLogin(){
        this.$router.push({path:'login'})
      }
    }
  }
</script>
<style lang="scss" scoped>
.reg-submit-warp{
  min-height: 300px;
  padding-top: 50px;
  text-align: center;
}
</style>