<template>
  <div class="activity bg3">
    <!--  -->
    <Header></Header>
    <div class="activity container containerBgColor">

      <!--  -->
        <div class="activity-list">
          <div class="find-paixu" style="width:568px;">
            <span class="mlr" :class="{'hover': type=='0'}" @click="changeSearType('0')"></span>
            <span class="mlr" :class="{'hover': type=='1'}" @click="changeSearType('1')"></span>
            <span class="mlr" :class="{'hover': type=='2'}" @click="changeSearType('2')"></span>
            <span class="mlr" :class="{'hover': type=='3'}" @click="changeSearType('3')"></span>
            <!--  -->
            <div class="mlr" style="width:200px;display:inline-block;">
              <treeselect v-model="value" :disable-branch-nodes="true" :show-count="true" :options="cityData" placeholder=""/>
            </div>
          </div>
          <div style="clear:both"></div>
          <div class="findTag">
            <el-tag type="success"><span class="el-icon-plus"></span></el-tag>
            <el-tag type="info"><span class="el-icon-plus"></span></el-tag>
            <el-tag type="warning"><span class="el-icon-plus"></span></el-tag>
            <el-tag type="danger"><span class="el-icon-plus"></span></el-tag>
            <el-tag type="info"><span class="el-icon-plus"></span></el-tag>
          </div>

          <el-row :gutter="50">
            <el-col :span="8" v-for="(o, index) in activeList" :key="index">
              <el-card :body-style="{ padding: '0px'}">
                  <div class="activity-item" @mouseover="changeTransitionFlag(index,false)" @mouseleave="changeTransitionFlag(index,true)">
                    <div class="activity-item-desc"  v-show="o.show1" key="box1">
                      <router-link :to="{name:'activityinfo',query: { 'active_id': o.active_id }}" active-class="activeClass">
                        <p class="tit-name">{{o.title}}</p>
                        <p class="tit-juban">{{o.organization_name}}</p>
                        <p class="tit-time">{{o.start_time}}</p>
                        <p class="tit-time">{{o.end_time}}</p>
                        <p class="tit-addres">{{o.addr}}</p>
                        <p class="desc-teacher">{{o.desc}}</p>
                        <div class="status">
                          <!-- <span></span> -->
                          <el-button v-if="o.status == 1" class="danger"></el-button>
                          <el-button v-if="o.status == 2" class="success"></el-button>
                          <el-button v-if="o.status == 3" class="info"></el-button>
                        </div>
                      </router-link>
                    </div>
                    <!-- banner  -->
                     <el-collapse-transition >
                      <div v-show="o.show" style="position:relative" key="box2">
                          <div class="activity-item-banner">
                            <el-image
                                style="width: 100%; height: 365px;"
                                src="111"
                                fit="cover">
                                <div slot="placeholder" class="image-slot">
                                <span class="dot">...</span>
                                </div>
                            </el-image>
                            <div class="tag">
                              <el-tag type="success"></el-tag>
                              <el-tag type="info"></el-tag>
                            </div>
                          </div>
                      </div>
                    </el-collapse-transition>
                    
                  </div>
                  
              </el-card>
            </el-col>
          </el-row>
        </div>
    </div>
    <!--  -->
    <Footer></Footer>
  </div>
  
</template>

<script>
import Header from '../components/Header.vue'
import Footer from '../components/Footer.vue'
import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
import {apiGetActiveList} from '../api'
import citys from '../assets/data/citys.js'

export default {
  name: 'find',
  data() {
    const generateData = _ => {
        const list = [];
        for (let i = 1; i <= 15; i++) {
            var item = {
                type:'1',
                key: i,
                show: true,
                show1: false,
                desc: ` ${ i }`,
            }
            list.push(item);
        }
        return list;
    };
    return {
      load: false,
      activeName: 'first',
      list: generateData(),
      activeList: [],
      pageConfig:{
        page: 1,
        page_size: 20,
        total_num: 0
      },
      type: '',
      value: null,
      cityData: citys.citys
    }
  },
  mounted() {
    // 
    this.getActiveList()
  },
  components: {
    Header,
    Footer,
    Treeselect
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    format(percentage) {
      return percentage === 100 ? '${percentage}' : `${percentage}`;
    },
    changeTransitionFlag(index, flag){
      this.activeList[index].show = flag
      this.$set(this.activeList, index, this.activeList[index])
      if(flag){
        this.activeList[index].show1 = !flag
        this.$set(this.activeList, index, this.activeList[index])
      }else{
        let _this = this
        setTimeout(function(){
          _this.activeList[index].show1 = !flag
          _this.$set(_this.activeList, index, _this.activeList[index])
        }, 300)
      }
      
    },
    changeSearType(value){
      this.type = value
      if(value || value == 0){
        this.getActiveList()
      }
    },
    // 
    getActiveList(){
      apiGetActiveList({
        'title': this.title,
        'page': this.pageConfig.page,
        'page_size': this.pageConfig.page_size,
        'type': this.type
      }).then( res => {
        if(res.data.code==200){
          this.activeList = res.data.data.list
          this.activeList.forEach(item =>{
            item.show = true
            item.show1 = false
          })
          this.load = true
          // 
          this.pageConfig.total_num = parseInt(res.data.data.total_num)
        }else{
          this.activeList = {}
        }
      })
    }
  }
}
</script>

