<template>
  <div class="userInfo">
    <div class="top-title-warp">
        <span class="top-title-line"></span>
        <span class="top-title-text"></span>
    </div>
    <el-card class="box-card">
        <el-form ref="ruleForm" :model="ruleForm" label-width="100px">
          <el-row>

            <el-col :span="24">
              <el-form-item label="" required>
                <el-upload
                  action="''"
                  class="avatar-uploader"
                  accept="image/jpeg,image/png,image/jpg"
                  :show-file-list="false"
                  :http-request="uploadTitleImg"
                  :on-change="imageChange"
                >
                  <img v-if="ruleForm.headImg" :src="ruleForm.headImg" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
            </el-col>

            <el-col :span="24">
              <el-form-item label="" prop="title" required>
                <el-input v-model="ruleForm.title"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="" prop="addr" required>
                  <treeselect v-model="ruleForm.cityId" :disable-branch-nodes="true" :show-count="true" :options="cityData" placeholder=""/>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="" prop="addrinfo" required>
                <el-input v-model="ruleForm.addrinfo"></el-input>
              </el-form-item>
            </el-col>
         
            <el-col :span="12">
              <el-form-item label="" prop="startTime" required>
                <el-date-picker
                    v-model="ruleForm.startTime"
                    type="date"
                    placeholder="">
                </el-date-picker>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="endTime" required>
                <el-date-picker
                    v-model="ruleForm.endTime"
                    type="date"
                    placeholder="">
                </el-date-picker>
              </el-form-item>
            </el-col>

            <el-col :span="24">
                <el-form-item
                  label=""
                  :rules="{required: true, message: '', trigger: 'change'}"
                >
                  <el-checkbox-group v-model="ruleForm.tag" style="text-align:left;">
                    <el-checkbox-button v-for="item in tagList" :key="item" :label="item">
                      {{ item }}
                    </el-checkbox-button>
                  </el-checkbox-group>                
                </el-form-item>
            </el-col>
            <el-col :span="24">
              <el-form-item label="" prop="desc">
                <el-input v-model="ruleForm.desc" type="textarea" class="org-release-desc"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
    </el-card>

    <div class="btns">
      <el-button type="primary" @click="onSubmit" round>&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</el-button>
    </div>
  </div>

</template>
<script>
import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
import citys from '../assets/data/citys.js'
import { 
  apiGetOrganizationList,
  apiGetSiteConfig,
  apiOrgPublish,
  apiPostUploadFile
} from "../api";

  export default {
    name: 'userinfo',
    mounted(){
    },
    created() {
    },
    data() {
      return {
        ruleForm:{
            headImg: '',
            title: '',
            startTime: '',
            endTime: '',
            desc:'',
            tag: [],
            addr:''
        },
        tagList:['','','','',''],
        checkImg: true,
        rules: {
            title: [
            { required: true, message: "", trigger: "blur" }
            ],
            startTime: [{ required: true, message: "", trigger: "blur" }],
            endTime: [{ required: true, message: "", trigger: "blur" }],
            desc: [{ required: true, message: "", trigger: "blur" }],
            addr:[{ required: true, message: "", trigger: "blur" }]
        },
        cityData: citys.citys
      }
    },
    computed: {
      uploadText(){
        if(this.ruleForm.headImg){
          return ''
        }
        return ''
      }
    },
    components: {
      Treeselect
    },
    methods: {
      imageChange(file) { 
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
          this.$message.error(" 2MB!");
          this.checkImg = false
        } 
      },
      // 
      uploadTitleImg(event) {
        const file = event.file
        const param = new FormData() // form
        param.append('file', file) // appendform
        const config = {
          headers: {'Content-Type': 'multipart/form-data'}
        }
        if(this.checkImg){
          apiPostUploadFile(param, config).then(res => {
            if (res.data.code == 200) {
              this.ruleForm.headImg = res.data.data.file_path    
            }else{
              this.$message.error(res.data.msg);
            }
          })
        }
      },
      onSubmit(){
        let params = {
          userId: this.GLOBAL.userId,
          token: this.GLOBAL.token,
          headImg: this.ruleForm.headImg,
          startTime: this.ruleForm.startTime,
          endTime: this.ruleForm.endTime,
          desc: this.ruleForm.desc,
          cityId: this.ruleForm.cityId,
          addr: this.ruleForm.addr
        }
        this.$refs['ruleForm'].validate((valid) => {
            apiOrgPublish(params).then(res=>{
                if (res.data.code == 200) {
                    this.$message.success('');
                }else{
                    this.$message.error(res.data.msg);
                }
            })
        })
        
      }
    }
  }
</script>
<style lang="scss" scoped>
    .userInfo{
        margin: 20px;
    }

    .top-title-warp {
        width: 100%;
        max-width: 1400px;
